import { useEffect, useState } from 'react';

export default function StockReportDashboard() {
    const [report, setReport] = useState<any[]>([]);

    useEffect(() => {
        fetch('/api/stock-report')
            .then(res => res.json())
            .then(setReport)
            .catch(console.error);
    }, []);

    return (
        <div>
            <h2>Stock Report</h2>
            <table>
                <thead>
                    <tr>
                        <th>Product</th>
                        <th>Location</th>
                        <th>Opening</th>
                        <th>Receipts</th>
                        <th>Sales</th>
                        <th>Transfers In</th>
                        <th>Transfers Out</th>
                        <th>Returns</th>
                        <th>Damaged</th>
                        <th>Expired</th>
                        <th>Rebag Gain</th>
                        <th>Rebag Loss</th>
                        <th>Closing</th>
                    </tr>
                </thead>
                <tbody>
                    {report.map((r, idx) => (
                        <tr key={idx}>
                            <td>{r.productName}</td>
                            <td>{r.locationName}</td>
                            <td>{r.openingStock}</td>
                            <td>{r.receipts}</td>
                            <td>{r.sales}</td>
                            <td>{r.transfersIn}</td>
                            <td>{r.transfersOut}</td>
                            <td>{r.returns}</td>
                            <td>{r.damaged}</td>
                            <td>{r.expired}</td>
                            <td>{r.rebagGain}</td>
                            <td>{r.rebagLoss}</td>
                            <td>{r.closingStock}</td>
                        </tr>
                    ))}
                </tbody>
            </table>
        </div>
    );
}
